import React, { useEffect } from 'react'
import { Container, Row } from 'react-bootstrap'
import MoviesDetailsImg from '../Component/Movies/MoviesDetailsImg'
import MoviesDetailsText from '../Component/Movies/MoviesDetailsText'
import { useDispatch, useSelector } from 'react-redux'
import { getMovies, getOneMovies } from '../Redux/Actions/moviesActions'
import { useParams } from 'react-router-dom'
import MoviesCard from '../Component/Movies/MoviesCard'

const DetailsPage = () => {
    const params = useParams()
    const dispatch = useDispatch()
    useEffect(() => {
        dispatch(getOneMovies(params.id))
        const reload = async () => {
            await dispatch(getMovies())

        }
        reload()

    }, [params.id]);

    const state = useSelector(state => state.movies)
    const moveDetailis = useSelector(state => state.oneMovie)
    const loading = useSelector(state => state.loading)

    return (
        <Container className=''>
            <Row className='m-0 ' >
                {loading ? (<div className="spinner-border my-2 mx-auto" role="status">
                    <span className="visually-hidden mx-auto">Loading...</span>
                </div>) : (

                    <Row className='p-0 my-3 m-0 d-flex justify-content-between shadow'>
                        <MoviesDetailsImg moveDetailis={moveDetailis} />
                        <MoviesDetailsText moveDetailis={moveDetailis} />
                    </Row>
                )
                }

                <div className='text-center my-3 mx-0'>
                    <h3 className='title my-1'>more Movies</h3>
                    <hr className='hr' />
                </div>
                <Row className='disply_row m-0 py-3  '>
                    {
                        loading ? (<div className="spinner-border  mx-auto" role="status">
                            <span className="visually-hidden mx-auto">Loading...</span>
                        </div>) : (
                            state ? (state.map((item) => {
                                return (
                                    <MoviesCard key={item.id} mov={item} />
                                )
                            })) : (null)
                        )

                    }

                </Row>



            </Row >

        </Container >
    )
}

export default DetailsPage